import React, { useState } from "react";
import { Image } from "../../types";
import Slick from "react-slick";
import {
  Overlay,
  Header,
  CloseBtn,
  SlickWrapper,
  ImgWrapper,
  Indicator,
  Global,
} from "./styles";

interface ImagesZoomProps {
  images: Image[];
  onClose: () => void;
}

const ImagesZoom: React.FC<ImagesZoomProps> = ({ images, onClose }) => {
  const [currentSlide, setCurrentSlide] = useState(0);

  return (
    <Overlay>
      <Global />
      <Header>
        <h1> </h1>
        <CloseBtn onClick={onClose} />
      </Header>
      <SlickWrapper>
        <Slick
          initialSlide={0} //  
          afterChange={(slide) => setCurrentSlide(slide)} //   state
          infinite // 
          arrows={false} //  
          slidesToShow={1} //   
          slidesToScroll={1} //  
        >
          {images.map((v) => (
            <ImgWrapper key={v.src}>
              <img src={v.src} alt={v.src} />
            </ImgWrapper>
          ))}
        </Slick>
        <Indicator>
          <div>
            {currentSlide + 1} /{images.length}
          </div>
        </Indicator>
      </SlickWrapper>
    </Overlay>
  );
};

export default ImagesZoom;
